@keyframes rotate-orbit {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-orbit-min {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

body {
  margin: 0;
}

.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.page__stopwatch {
  background-color: #99edf6;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__item1 {
  z-index: 5;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-orbit-min;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch__item2 {
  z-index: 0;
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-orbit;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

.stopwatch--speed-up .stopwatch__item1 {
  animation: 600s steps(60, end) infinite rotate-orbit-min;
}

.stopwatch--speed-up .stopwatch__item2 {
  animation: 10s linear infinite rotate-orbit;
}

.stopwatch__item3 {
  z-index: 10;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
